function FullScreenDialog() {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <React.Fragment>
      <Fab color="secondary" aria-label="add" className={classes.fabButton} onClick={handleClickOpen}>
            <AddIcon />
      </Fab>       
      <Dialog fullScreen open={open} onClose={handleClose} TransitionComponent={Transition}>
        <AppBar className={classes.appBar}>
          <Toolbar>
            <IconButton edge="start" color="inherit" onClick={handleClose} aria-label="close">
              <CloseIcon />
            </IconButton>
            <Typography variant="h6" className={classes.title}>
              Sound
            </Typography>
            <Button autoFocus color="inherit" onClick={handleClose}>
              save
            </Button>
          </Toolbar>
        </AppBar>
        <List>
          <ListItem button>
            <ListItemText primary="Phone ringtone" secondary="Titania" />
          </ListItem>
          <Divider />
          <ListItem button>
            <ListItemText primary="Default notification ringtone" secondary="Tethys" />
          </ListItem>
        </List>
      </Dialog>
    </React.Fragment>
  );
}

function JobItemMenu() {
  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <ListItemSecondaryAction >
      <IconButton edge="end" aria-label="menu" aria-controls="job-item-menu" aria-haspopup="true" onClick={handleClick}>
        <MoreIcon />
      </IconButton>
      <Menu
        id="job-item-menu"
        anchorEl={anchorEl}
        keepMounted
        open={Boolean(anchorEl)}
        onClose={handleClose}
      >
        <MenuItem onClick={handleClose}>Profile</MenuItem>
        <MenuItem onClick={handleClose}>My account</MenuItem>
        <MenuItem onClick={handleClose}>Logout</MenuItem>
      </Menu>
    </ListItemSecondaryAction>
  );
}

function ProfileMenu() {
  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <React.Fragment>
      <IconButton edge="end" aria-label="menu" color="inherit" aria-controls="profile-menu" aria-haspopup="true" onClick={handleClick}>
        <AccountCircleIcon />
      </IconButton>
      <Menu
        id="profile-menu"
        anchorEl={anchorEl}
        keepMounted
        open={Boolean(anchorEl)}
        onClose={handleClose}
      >
        <MenuItem onClick={handleClose}>Профиль</MenuItem>
        <MenuItem onClick={handleClose}>Перейти</MenuItem>
      </Menu>
    </React.Fragment>
  );
}


export default function BottomAppBar() {
  const classes = useStyles();


  return (
    <React.Fragment>
      <CssBaseline />
      <AppBar position="sticky" color="primary">
        <Toolbar>
          <img src="https://i2.wp.com/nopcproblem.ru/wp-content/uploads/2019/05/discord-logo.png?ssl=1" alt="logo" className={classes.logo} />
          <Typography variant="h6" className={classes.title}>
            Dinabot
          </Typography>
          <div className={classes.grow} />
          <IconButton color="inherit" aria-label="open drawer">
            <MenuIcon />
          </IconButton>          
          <IconButton color="inherit">
            <GroupIcon />
          </IconButton>
          <ProfileMenu />
        </Toolbar>
      </AppBar>
      <Paper square className={classes.paper}>
        <Typography className={classes.text} variant="h5" gutterBottom>
          Задания боту для текущей таблицы:
        </Typography>
        <List className={classes.list}>
          {//if(messages.jobs && messages.jobs.length>0){
            messages.workspace.jobs.map(job => (
            <React.Fragment key={job.id}>
              <ListItem button>
                <ListItemAvatar>
                  <AssessmentIcon />
                </ListItemAvatar>
                <ListItemText primary={job.name} secondary = {JobItemDesc(job)} />                
                <JobItemMenu>
                </JobItemMenu>
              </ListItem>
            </React.Fragment>
          ))
          
          
          }

        </List>
       
      </Paper>
          <FullScreenDialog />

    </React.Fragment>
  );
}